import { createAuthLink, AUTH_TYPE } from 'aws-appsync-auth-link';
import { createSubscriptionHandshakeLink } from 'aws-appsync-subscription-link';
import { ApolloClient, InMemoryCache, ApolloLink } from '@apollo/client';

import Auth from '@aws-amplify/auth';
import config from '../config';

const getJwtToken = async (): Promise<string> => (await Auth.currentSession()).getIdToken().getJwtToken();

const graphqlConfig = {
    url: config.awsAppSyncConfig.endpoint,
    region: config.awsAppSyncConfig.region,
    auth: {
        type: AUTH_TYPE.AMAZON_COGNITO_USER_POOLS,
        jwtToken: getJwtToken,
    },
};

const client = new ApolloClient({
    link: ApolloLink.from([
        // @ts-ignore
        createAuthLink(graphqlConfig),
        // @ts-ignore
        createSubscriptionHandshakeLink(graphqlConfig),
    ]),
    cache: new InMemoryCache(),
    defaultOptions: {
        watchQuery: {
            fetchPolicy: 'cache-and-network',
        },
    },
});

export { client };
